<template>
  <svg version="1.1" class="circle-progress" viewbox="0 0 100 100" preserveAspectRatio="xMidYMid meet" width="100%" height="100%" xmlns="http://www.w3.org/2000/svg">
    <circle class="progress-bg" :stroke-width="fillWidth + '%'" :cx="50 + '%'" :cy="50 + '%'" :r="circleRadius + '%'"></circle>
    <circle class="progress-bar" :stroke-width="fillWidth + '%'" :cx="50 + '%'" :cy="50 + '%'" :r="circleRadius + '%'" :style="{ 'transition-duration': duration + 'ms' }" :stroke-dasharray="len + '%'" :stroke-dashoffset="progress + '%'"></circle>
    <circle class="progress-dot" :cx="100 - fillWidth / 2 + '%'" :cy="50 + '%'" :r="fillWidth / 6 + '%'" fill="#fff"></circle>
  </svg>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 0
    },
    duration: {
      type: Number,
      default: 500
    },
    fillWidth: {
      type: Number,
      default: 14
    }
  },
  computed: {
    circleRadius () {
      return (100 - this.fillWidth) / 2
    },
    progress () {
      return (1 - (this.value > 1 ? this.value / 100 : this.value)) * this.len
    }
  },
  created () {
    this.len = Math.PI * 2 * this.circleRadius
  },
  data () {
    return {
      len: 0
    }
  }
}
</script>

<style lang="less" scoped>
.circle-progress {
  box-sizing: border-box;
}
.progress-bg {
  stroke: #f1f1f6;
  // transform: rotateX(180deg);
  fill: none;
  box-sizing: border-box;
}
.progress-bar {
  stroke: #f69626;
  fill: none;
  stroke-linecap: round;
  transition: stroke-dasharray ease-in-out, stroke-dashoffset ease-in-out;
  transform: rotateX(180deg);
  transform-origin: center;
  box-sizing: border-box;
}
</style>
